@import '../../Global.scss';
.portfolio{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    h1{
        font-size: 50px;
        @include mobile{
            font-size: 20px;
        }
    }
    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;
        @include mobile{
            margin: 10px 0;
            flex-wrap: wrap;
            justify-content: center;
            align-items: center;
        }
        li{
            font-size: 14px;
            margin-right: 50px;
            padding: 7px;
            border-radius: 10px;
            cursor: pointer;
            @include mobile{
                margin-right: 20px;
        
            }
            &.active{
                background-color: $mainColor;
                color:white;
            }
            }
    }
    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        @include mobile{
            width: 100%;
        }
        .item{
            width: 300px;
            height: 250px;
            border-radius: 20px;
            border: 1px solid rgb(240,239,239);
            margin: 25px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all .5s ease;
            cursor: pointer;
            @include mobile{
               
               width: 130px;
               height: 100px;
            }
            box-shadow: 0 0 18px 8px grey;
               h3{
                position: absolute;
                font-size: 20px;
               }
               img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index:1;
                
               }
               &:hover{
                background-color: $mainColor;
                box-shadow: none;
                
                img{
                    opacity: 0.2;
                    z-index: 0;
                   
                }
               }
        }

    }
    
}